.app-header {
  position: sticky;
  top: 0;
  background-color: white;
}

.nav-bar {
  display: flex;
  height: 50px;
  align-items: center;
}

.nav-item {
  flex: 1;
  text-decoration: none;
  display: inline-block;
  color: black;
}

.nav-item:link {
  text-decoration: none;
}

.nav-item:visited {
  text-decoration: none;
}

.website-logo {
  font-weight: 600;
}